$navHeight: 50px;
$itemPadding: 10px;
$itemPaddingMobile: 10px;
$defaultRadius: 4px;

$colorLink: #90c3d4;

$font: "Fira Code", "Fira Mono", Menlo, Consolas, "DejaVu Sans Mono", monospace; // "8bit"; //"Icarus"; //"Maven Pro", "Open Sans", sans-serif;
$fontBold: "Mario"; //"Maven Pro Bold", "Open Sans Bold", sans-serif;
$fontBaseSize: 16px;
$fontColor: #123;
$fontMenu: "8bit";
$fontCode: "Fira Code";

/* Gradients */
$gradientAngle: -30deg;
$colorBackground: linear-gradient($gradientAngle, #0093e9 0%, #80d0c7 100%);
$colorBackground: linear-gradient($gradientAngle, #0093e9 0%, #80d0c7 100%);
$colorBackground: linear-gradient($gradientAngle, #1f4037 0%, #1f4037 100%);
$colorBackground: linear-gradient($gradientAngle, #1f4037 0%, #99f2c8 100%);

$colorBackground: linear-gradient($gradientAngle, #141e20 0%, #242b35 100%);
$colorBackground: linear-gradient($gradientAngle, #2f5047 0%, #99f2c8 100%);
$colorBackground2: linear-gradient($gradientAngle, #141e30 0%, #243b55 100%);

// COOL LIGHT:
//$colorBackground: linear-gradient($gradientAngle, #111827 0%, rgba(17, 24, 39, 0) 100%);

// COOL DARK:
$colorBackgroundDark: linear-gradient($gradientAngle, #111827 0%, rgb(17, 24, 39) 100%);

// OR WHITE:
//$colorBackground: none;

// these should NOT use print out any real css, virtual only!
// put non-virtual variables in app.scss

// TODO: these CSS vars should load the vars from actuall SASS variables, ie. $, so the variables can be passed around at compile time more easily, and still received here.
@import "./mixins";

:root {
    --font-size-default: 16px;
    --font-default: "Icarus"; //"Avenir Light", "Myriad Pro", "Open Sans";
    --font-bold-default: "Mario"; // "Myriad Pro", "Open Sans Bold";
    --font-input: "Icarus"; //"Andale Mono"; //var(--font-default);
    --font-dropdown: var(--font-input);
    --font-button: var(--font-input);

    /* color definitions */
    --color-font: var(--color-grey);
    --color-text: var(--color-font);
    --color-header: #ace398;
    --color-subheader: #fff;

    --color-error: var(--color-red);
    --color-error-dark: var(--color-red-dark);

    --input-color: var(--color-grey-1);
    --input-color-placeholder: var(--color-grey-4);
    --input-color-dark: var(--color-grey-9);
    --input-color-placeholder-dark: var(--color-grey-7);

    --button-font: "Mario"; //"Grotesk Condensed";
    --button-font-size: 1rem;
    --button-text-color: var(--color-blue-light);
    --button-text-color-hover: white;
    --button-color-bg: none;
    --button-background-color: #0069d2;
    --button-height: 2rem;
    --button-radius: calc(var(--button-height) / 3.5);
    --button-border-width: 2px;
    --button-bar-height: var(--button-height);
    //////////////////////////////

    --caret-color: white;
    --caret-color-dark: var(--color-blue-dark);

    --input-padding: 0.75rem;
    --input-font-size: 1.25rem;
    --input-radius: 5px;

    --modal-radius: calc(2 * var(--button-radius));
    --modal-background: white;
    --modal-color-font: var(--color-grey-3);

    // Spacing / sizes:
    --header-height: 50px;

    --default-padding: 20px;
    --default-gutter: clamp(1rem, 1vw, 3rem);

    // old (refactor):
    --default-gutter: clamp(1rem, 1vw, 3rem);
    --default-gutter-static: 15px;
    --gutter-large: clamp(2rem, 2vw, 6rem);

    --dropdown-background: rgba(20, 38, 67, 1);
    --dropdown-border-color: #fff;
    --dropdown-border-width: 1px;
    --dropdown-shadow: 1px 1px 10px rgba(0, 0, 0, 0.25);
    --dropdown-shadow-light: 1px 0px 5px rgba(0, 0, 0, 0.1);

    // (dont use these, deprecated):
    --default-item-padding: 15px;

    --defaultPagePadding: 20px;
    --defaultPagePaddingTop: 50px;

    --defaultButtonPadding: 0.7em 1.1em;
    --defaultButtonHeight: 45px;

    --defaultInputPadding: 10px;

    --defaultRadius: 3px;

    --fontSizeInput: 1em;

    --animDefault: 0.35s;

    //--------------------------------------------------------------------------------

    // new version (use these):

    --color-red: #ff7158;
    --color-green: #66ffcc;
    --color-green-2: #3ec692;
    --color-green-3: #2af378;
    --color-blue-3: #89e0f4;
    --color-blue-2: #37c6f7;
    --color-blue: #3976ee;
    --color-purple: #623fea;
    --color-magenta: #a61fe5;
    --color-magenta-2: #f33fff;
    --color-orange: #eaa824;
    --color-white: #ffffff;
    --color-black: #000000;

    --color-link: #90c3d4; // lighten(#90c3d4, 15%);
    --color-link-hover: #90c3d4;
    --color-font: #fff;
    --color-font-grey: #333;
    --color-background: #fff; //#123;

    --button-radius: var(--default-radius);
    --button-color-bg: var(--color-blue);
    --button-color-font: var(--color-white);
    --button-color-bg-hover: var(--color-blue);
    --button-text-color: var(--color-blue);
    --button-text-color-hover: white;

    --input-radius: var(--default-radius);
    --input-padding: 10px;
    --input-font-size: 1.18rem;

    // Text sizing:
    --font-size-h1: 1rem;
    --font-size-h2: 1.2;

    --default-gutter: 1vw;

    @include mobile {
        --defaultPagePadding: 10px;
        --default-item-padding: 10px;
        --defaultRadius: 7px;

        // new:

        --header-height: 85px;

        --default-gutter: 4vw;
        --default-gutter-static: 15px;
        --gutter-large: 8vw;

        --default-padding: 15px;
    }
}
