/* sets browser standards */

* {
    box-sizing: border-box;
}

html,
body {
    min-height: 100%;
    max-width: 100%;
    margin: 0;
    padding: 0;
    font-size: 16px;
    overflow-x: hidden;
}

a {
    text-decoration: none;
}

ul,
li {
    margin: 0;
    padding: 0;
    list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

input,
select,
textarea,
iframe {
}

